@import '../node_modules/pixel-borders/src/styles/pixel-borders.scss';
$imgSize: 128px;
.Main {
  height: 100%;

  /*   background-image: linear-gradient(
    180deg,
    #7ba573 25%,
    #4a7342 25%,
    #4a7342 50%,
    #7ba573 50%,
    #7ba573 75%,
    #4a7342 75%,
    #4a7342 100%
  ); */

  .pokedex-title {
    ::after {
      content: '';
      width: 500px;
      height: 600px;
      background: rgba(255, 0, 0, 1);
    }
    @include pixel-borders(
      $corner-size: 2,
      $border-size: 2px,
      $border-color: #000
    );
    border-style: solid;
    border-width: 8px;
    border-radius: 40px;
    border-color: #000;
    background-color: aliceblue;
    background-clip: padding-box;
    text-align: center;

    margin: auto;

    h1 {
      margin: 0px;
      padding: 1em;
    }
  }

  .dex {
    $dexHeight: 80vh;
    z-index: 1;
    position: relative;
    /*  top: -25vh; */
    height: $dexHeight;
    width: fit-content;

    overflow-y: scroll;
    margin: auto;
    background-color: #eff739;
    margin-bottom: 50px;
    margin-top: 50px;
    .dexImagesBox {
      z-index: 2;
      width: fit-content;
      height: $dexHeight;
      margin: 0 0.8em 0 0;
      float: left;
      .dexPicture {
        z-index: 1;
        width: fit-content;
        margin: 0;
        height: $imgSize;
        img {
          z-index: 1;
          height: $imgSize;
          width: $imgSize;
          position: relative;
          image-rendering: pixelated;
          object-fit: cover;
          object-position: 0 15%;
        }
      }
    }
    .dexEntriesBox {
      float: left;
      margin-right: 0.8em;

      .dexEntry {
        margin-top: ($imgSize/(1024/425) / 2);
        padding-bottom: ($imgSize/(1024/425) / 2);
        width: 100%;

        .dexData {
          display: flex;
          align-items: center;
          gap: 1.5em;
          // Remove button styling
          background: none;
          color: inherit;
          border: none;
          padding: 0;
          font: inherit;
          cursor: pointer;
          outline: inherit;

          width: 100%;
          font-size: calc($imgSize / 4) + 'px';
          // Egg
          img {
            height: calc($imgSize / 4);
          }
        }
        transition: all 1s ease-in-out;
        background-color: #eff739;
        border: 0px;
        :hover {
          transition: linear;
          background-color: white;
          border-radius: 13px;
        }
      }
    }
  }
}

.infoBackground {
  button {
    background: none;
    color: inherit;
    border: none;
    padding: 0;
    font: inherit;
    cursor: pointer;
    outline: inherit;
    color: white;
    span {
      font-size: 5rem;
      width: fit-content;
      position: relative;
      top: $imgSize;
    }
  }
  height: 100vh;
  width: 100vw;
  background-color: rgba(0, 0, 0, 0.4);
  .dexPicture {
    z-index: 1;
    width: fit-content;
    margin: auto;
    height: $imgSize;
    img {
      z-index: 1;
      height: $imgSize;
      width: $imgSize;
      position: relative;
      image-rendering: pixelated;
      object-fit: cover;
      object-position: 0 15%;
    }
  }
  .infoBox {
    margin: auto;
    padding: 2em;
    width: 80%;
    background-color: white;
    text-align: center;
  }
}
